* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
a:visited,
a:hover {
  text-decoration: none;
  outline: none;
}

a:focus-visible,
button:focus-visible {
  outline: 2px solid #3E9CDC;
  outline-offset: 3px;
}

aside,
nav,
footer,
header,
section {
  display: block;
}

html,
body {
  height: 100%;
  min-width: 320px;
}

body {
  font-size: 18px;
  line-height: 1;
  font-family: "Lato", sans-serif;
  color: #FFFFFF;
  font-weight: 400;
  font-style: normal;
}

input,
button,
textarea {
  font-size: inherit;
  font-family: "Lato", sans-serif;
  outline: none;
}

button {
  background-color: transparent;
  cursor: pointer;
  font-weight: 500;
}

ul li {
