.btn {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border-radius: 4px;
  padding: 10px 15px;
  text-transform: uppercase;
  transition: color 0.3s ease, background-color 0.3s ease, border-color 0.3s ease;
}

.btn-blue {
  color: #FFFFFF;
  background-color: #3E9CDC;
  border: 1px solid #FFFFFF;
}
@media (any-hover: hover) {
  .btn-blue:hover {
