    border: 1px solid #FFFFFF;
  }
}

.choices {
  width: 100%;
  height: 100%;
  margin-bottom: 0;
  border-bottom: none;
}
.choices.is-open .choices__list--single .choices__placeholder {
  color: #FFFFFF;
}
.choices.is-open .choices__inner {
  background-color: #3D4050;
  border-color: #3D4050;
}
.choices.is-open .choices__item--selectable[aria-selected=true] {
  color: #FFFFFF;
}
.choices:focus-visible {
  outline: 2px solid #3E9CDC;
  outline-offset: 3px;
}

.choices__inner {
  height: 100%;
  margin: 0;
  padding: 0;
  padding: 15px 0 0 10px;
  background-color: rgba(255, 255, 255, 0.85);
  border: 1px solid #FFFFFF;
  border-radius: 3px;
}

.choices[data-type*=select-one]::after {
  top: 47%;
  right: 16px;
  height: 8px;
  width: 8px;
  z-index: 10;
  border: 1px solid #3D4050;
  border-width: 0 0 2px 2px;
  transform: rotate(-45deg);
  transition: transform 0.3s ease, margin-top 0.3s ease, color 0.3s ease;
}

.choices[data-type*=select-one].is-open::after {
  margin-top: -2px;
  transform: rotate(-225deg);
  border-color: #FFFFFF;
}

.choices__list--single {
  padding: 0;
}

.choices__list--single .choices__placeholder {
  display: flex;
  justify-content: space-between;
  color: #272733;
  font-size: 18px;
  opacity: 1;
  transition: color 0.3s ease;
}

.choices__list--dropdown .choices__placeholder {
  display: none;
}

.choices__item {
  display: flex;
  justify-content: flex-start;
  color: #FFFFFF;
}

.choices__list[aria-expanded] {
  z-index: 100;
  min-height: 172px;
  background-color: #272733;
  border: 1px solid #3D4050;
  height: 100%;
  overflow-y: auto;
}

.is-open .choices__list--dropdown, .is-open .choices__list[aria-expanded] {
  border-color: #3D4050;
}

.choices__list[aria-expanded] .choices__item--selectable.is-highlighted {
  color: #3E9CDC;
  background-color: #272733;
  transition: color 0.3s ease;
}

.simplebar-track.simplebar-vertical {
  width: 7px;
  background: #3D4050;
  border-radius: 3px;
}

.simplebar-scrollbar.simplebar-visible:before {
  opacity: 1;
  width: 7px;
  background-color: #3E9CDC;
  border-radius: 3px;
  top: 0;
  left: 0;
}

.choices__item--selectable[aria-selected=true] {
  color: #272733;
  transition: color 0.3s ease;
}

.choices__list--dropdown .choices__item--selectable, .choices__list[aria-expanded] .choices__item--selectable {
  padding-right: 10px;
}

.choices__list--dropdown .choices__item, .choices__list[aria-expanded] .choices__item {
  padding-left: 12px;
}

.hidden {
  overflow: hidden;
}

