.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: rgba(16, 16, 29, 0.9);
  transition: box-shadow 0.3s ease;
}
.header.active {
  box-shadow: 0 0 10px 10px rgba(255, 255, 255, 0.5);
}
.header__body {
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 100px;
  padding: 10px;
}
@media (max-width: 600px) {
  .header__body {
    min-height: 70px;
  }
}
.header__logo {
  font-family: "Raleway";
  font-weight: 800;
  font-size: 28px;
  text-transform: uppercase;
  color: #FFFFFF;
  margin-right: 20px;
}
@media (max-width: 600px) {
  .header__logo {
    font-size: 20px;
  }
}
.header__logo span {
  color: #43ABF0;
}
@media (max-width: 767.9px) {
  .header__menu {
    display: flex;
    flex-direction: column;
    position: fixed;
    height: 100%;
    width: 40%;
    top: 0;
    left: 0;
    transform: translateX(-100%);
    overflow-y: auto;
    background-color: rgba(16, 16, 29, 0.95);
    box-shadow: 10px 10px 20px rgba(255, 255, 255, 0.5);
    z-index: 50;
    padding: 50px 20px;
    opacity: 0;
    visibility: hidden;
    transition: all 0.5s ease;
  }
  .header__menu.active {
    opacity: 1;
    visibility: visible;
    transform: translateX(0);
    transition: all 0.5s ease;
  }
}
@media (max-width: 767.9px) {
  .header__menu {
    width: 60%;
  }
}
@media (max-width: 600px) {
  .header__menu {
    width: 80%;
  }
}
.header__list {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
@media (max-width: 767.9px) {
  .header__list {
    flex-direction: column;
    align-items: flex-start;
  }
}
.header__item {
  margin: 5px 0;
}
.header__item:not(:last-child) {
  margin-right: 60px;
}
@media (max-width: 991.9px) {
  .header__item:not(:last-child) {
    margin-right: 40px;
  }
}
@media (max-width: 767.9px) {
  .header__item {
    margin: 10px 0;
  }
}
.header__link {
  font-weight: 600;
  font-size: 13px;
  text-transform: uppercase;
  color: #FFFFFF;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .header__link:hover {
    color: #43ABF0;
  }
}
@media (max-width: 767.9px) {
  .header__link {
    font-size: 24px;
  }
}
.header__burger {
  display: none;
}
@media (max-width: 767.9px) {
  .header__burger {
    position: relative;
    display: block;
    width: 25.5px;
    height: 18.5px;
    cursor: pointer;
    z-index: 10;
  }
  .header__burger span {
    display: block;
    position: absolute;
    top: calc(50% - 1px);
    left: 0;
    height: 2px;
    width: 19px;
    border-radius: 2px;
    background-color: #FFFFFF;
    transition: transform 0.3s ease, background-color 0.3s ease;
  }
  .header__burger span:nth-child(1) {
    width: 100%;
    top: 0;
  }
  .header__burger span:last-child {
    width: 12px;
    top: auto;
    bottom: 0;
  }
  .header__burger.active span {
    transform: scale(0);
  }
  .header__burger.active span:first-child {
    transform: rotate(45deg);
    top: calc(50% - 1px);
  }
  .header__burger.active span:last-child {
    width: 100%;
    transform: rotate(-45deg);
    bottom: calc(50% - 1px);
  }
}
@media (max-width: 600px) {
  .header__burger {
    width: 19.5px;
    height: 14.5px;
  }
  .header__burger span {
    width: 14.5px;
  }
  .header__burger span:nth-child(1) {
    width: 100%;
    top: 0;
  }
  .header__burger span:last-child {
    width: 9.5px;
  }
}
@media (any-hover: hover) {
  .header__burger:hover span {
    background-color: #43ABF0;
  }
}

